import React, {useState} from 'react';
import {Link} from "react-router-dom";
import Fade from 'react-reveal/Fade';
import { AnimateOnChange } from 'react-animation';

// Styling Import
import '../Styling/css/default.min.css';
import gerald from "../Images/headshot.jpg";

// Component Import
import Logo from "./SmallComponents/Logo";
import BlackWhiteDivider from "./SmallComponents/BlackWhiteDivider";

function About() {
    const generalText = "Hello! My name is Gerald Wang and I’m a former student at Rice University! I graduated with a degree in Computer Science and Cognitive Science. When I design applications, I always keep asking myself if there's a better way to approach the problem. Also, I encourage working on projects and tackling challenges as a team (the more the merrier right?). Right now, I’m working on a project aimed at bringing the performing arts into the mainstream space with an online platform aptly named A440!";
    const youtubeText = "In my spare time, I like to work on other passion projects of mine such as my YouTube channel! I create content that aims to help students learn more about college in addition to using it as a space to showcase my work in videography.";
    const activitiesText = "When I’m not coding or making videos, I like to learn new cooking techniques and dishes! I also love going to the gym and working out or playing basketball with my friends. If not that, I would spend time either reading or learning about the latest trends!";
    const [text, setText] = useState(generalText);
    const [section, setSection] = useState("general");

    return (
        <div className={"about-wrapper"}>
            <div className={"about"}>
                <Link to={"/"}>
                    <div className={"home-logo"}>
                        <Logo/>
                    </div>
                </Link>
                <div className={"about-info-wrapper"}>
                    <div className={"about-info"}>
                        <Fade left>
                            <div className={"about-info-picture"}>
                                <div className={"about-info-img-wrapper"}>
                                    <img src={gerald} className={"about-info-img"} alt={"A headshot of Gerald Wang"}/>
                                </div>
                                <div className={"about-info-icon-column"}>
                                    <div className={"about-info-icon-wrapper1"}>
                                        <div className={"about-info-icon"}
                                             onClick={() => {
                                                 setText(generalText);
                                                 setSection("general");
                                             }}
                                             style={section === "general"
                                                 ? {background: "var(--text-color-light)", color: "var(--text-color-dark)"}
                                                 : {}}>
                                            <i className="fa fa-user"/>
                                        </div>
                                    </div>
                                    <div className={"about-info-icon-wrapper2"}>
                                        <div className={"about-info-icon"}
                                             onClick={() => {
                                                 setText(youtubeText);
                                                 setSection("youtube");
                                             }}
                                             style={section === "youtube"
                                                 ? {background: "var(--text-color-light)", color: "var(--text-color-dark)"}
                                                 : {}}>
                                            <i className="fa fa-youtube-play"/>
                                        </div>
                                    </div>
                                    <div className={"about-info-icon-wrapper3"}>
                                        <div className={"about-info-icon"}
                                             onClick={() => {
                                                 setText(activitiesText);
                                                 setSection("activities");
                                             }}
                                             style={section === "activities"
                                                 ? {background: "var(--text-color-light)", color: "var(--text-color-dark)"}
                                                 : {}}>
                                            <i className="fa fa-smile-o"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fade>
                        <Fade right>
                            <div className={"about-info-details"}>
                                <AnimateOnChange durationOut={"150"}>
                                    <div className={"about-info-details-text"}>
                                        {text}
                                        {section === "youtube" &&
                                            <p style={{paddingTop: "20px"}}>
                                                You can find my YouTube channel&nbsp;
                                                <a href={"https://www.youtube.com/gerald_wang"}
                                                   target='_blank'
                                                   rel='noopener noreferrer'
                                                   className={"about-info-details-link"}>
                                                    here
                                                </a>
                                            </p>
                                        }
                                    </div>
                                    <div className={"about-info-divider"}>
                                        <BlackWhiteDivider/>
                                    </div>
                                </AnimateOnChange>
                            </div>
                        </Fade>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;