import React, {useEffect, useState} from 'react';
import FadeIn from "react-fade-in";
import {Link} from "react-router-dom";

// Styling Import
import '../Styling/css/default.min.css';
import Logo from "./SmallComponents/Logo";
import BlackWhiteDivider from "./SmallComponents/BlackWhiteDivider";
import {hspace} from "./SmallComponents/Spacer";

// Component Import

function NotFound() {
    const [background, setBackground] = useState("var(--color-home)");
    const [style, setStyle] = useState(
        {
            backgroundColor: background,
            transition: "0.3s"
        }
    );
    useEffect(() => {
        setStyle(
            {
                backgroundColor: background,
                transition: "0.3s"
            }
        )
    }, [background]);

    return (
        <div className={"home-wrapper"} style={style}>
            <div className={"home"}>
                <Link to={"/"}>
                    <div className={"home-logo"}>
                        <Logo/>
                    </div>
                </Link>
                <div className={"home-main"}>
                    <h1 className={"home-main-title"}>
                        Looks like this page doesn't exist.
                    </h1>
                    <div className={"home-main-divider"}>
                        <BlackWhiteDivider/>
                    </div>
                    <FadeIn className={"home-main-nav"} delay={100}>
                        <Link to={"/about"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => setBackground("var(--color-about)")}
                                onMouseLeave={() => setBackground("var(--color-home)")}>
                                About
                            </h2>
                        </Link>
                        {hspace(3)}
                        <Link to={"/portfolio"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => setBackground("var(--color-portfolio)")}
                                onMouseLeave={() => setBackground("var(--color-home)")}>
                                Portfolio
                            </h2>
                        </Link>
                        {hspace(3)}
                        <Link to={"/contact"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => setBackground("var(--color-contact)")}
                                onMouseLeave={() => setBackground("var(--color-home)")}>
                                Contact
                            </h2>
                        </Link>
                    </FadeIn>
                </div>
            </div>
        </div>
    );
}

export default NotFound;