import React, {useEffect, useState} from 'react';
import FadeIn from 'react-fade-in';
import {Link} from "react-router-dom";
import { AnimateOnChange } from 'react-animation';

// Styling Import
import '../Styling/css/default.min.css';

// Component Import
import Logo from "./SmallComponents/Logo";
import BlackWhiteDivider from "./SmallComponents/BlackWhiteDivider";
import {hspace} from "./SmallComponents/Spacer.js";

function Home() {
    const [firstLoad, setFirstLoad] = useState(true);
    const [title, setTitle] = useState("Hi, I'm Gerald and I make things.");
    const [background, setBackground] = useState("var(--color-home)");
    const [style, setStyle] = useState(
        {
            backgroundColor: background,
            transition: "0.3s"
        }
    );

    useEffect(() => {
        setStyle(
            {
                backgroundColor: background,
                transition: "0.3s"
            }
        )
    }, [background]);

    useEffect(() => {
        setFirstLoad(false);
    }, []);

    return (
        <div className={"home-wrapper"} style={style}>
            <div className={"home"}>
                <Link to={"/"}>
                    <div className={"home-logo"}>
                        <Logo/>
                    </div>
                </Link>
                <div className={"home-main"}>
                    {firstLoad
                        ? <h1 className={"home-main-title"}>
                            {title}
                        </h1>
                        : <AnimateOnChange durationOut={"150"}>
                            <h1 className={"home-main-title"}>
                                {title}
                            </h1>
                        </AnimateOnChange>
                    }
                    <div className={"home-main-divider"}>
                        <BlackWhiteDivider/>
                    </div>
                    <FadeIn className={"home-main-nav"} delay={100}>
                        <Link to={"/about"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => {
                                    setBackground("var(--color-about)");
                                    setTitle("I'm a developer, videographer, entrepreneur.");
                                }}
                                onMouseLeave={() => {
                                    setBackground("var(--color-home)");
                                    setTitle("Hi, I'm Gerald and I make things.");
                                }}>
                                About
                            </h2>
                        </Link>
                        {hspace(3)}
                        <Link to={"/portfolio"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => {
                                    setBackground("var(--color-portfolio)");
                                    setTitle("I’ve built projects and worked in industry.");
                                }}
                                onMouseLeave={() => {
                                    setBackground("var(--color-home)");
                                    setTitle("Hi, I'm Gerald and I make things.");
                                }}>
                                Portfolio
                            </h2>
                        </Link>
                        {hspace(3)}
                        <Link to={"/contact"}>
                            <h2 className={"home-main-nav-text"}
                                onMouseEnter={() => {
                                    setBackground("var(--color-contact)");
                                    setTitle("I’m always ready to talk.");
                                }}
                                onMouseLeave={() => {
                                    setBackground("var(--color-home)");
                                    setTitle("Hi, I'm Gerald and I make things.");
                                }}>
                                Contact
                            </h2>
                        </Link>
                    </FadeIn>
                </div>
            </div>
        </div>
    );
}

export default Home;